.next-button {
    border-radius: 7px;
    padding: 10px 30px;  
    font-size: 18px;
    background-color: dodgerblue;
    border: none;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    cursor: pointer;
    transition: all 0.3s;
    box-shadow: 5px 5px 3px 3px rgba(0, 0, 0, 0.2);
  }

  .next-button:disabled,
  .next-button[disabled] {
    background-color: #ccc;
    color: #666;
  }
  
  .next-button:hover {
    transform: scale(1.05);
  }
  
  .next-button i {
    font-size: 20px;
  }