.input-key {
    padding: 10px 0px;
    border-radius: 4px;
    font-size: 20px;
    text-align: center;
    border: 2px solid;
    border-color: transparent;
    transition: all 0.3s;
}

.input-key.ok {
    border-color: #86ff77;
}

.input-key.notok {
    border-color: #ff4d4d;
}

.input-key:focus {
    outline: none;
}